import React from 'react'

// material-ui
import { Box, Typography } from '@material-ui/core'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import useSiteMetaData from '../data/useSiteMetaData'

/**
 * NotFoundPage component
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function NotFoundPage({ location, navigate }) {
  const siteMeta = useSiteMetaData()
  return (
    <React.Fragment>
      <SiteHeader title="Opps - page not found" siteMeta={siteMeta} location={location} navigate={navigate} />
      <Box clone pt={16} pr={2} pb={2} pl={2}>
        <Typography variant="h5" color="inherit">
          {'Opps, You hit a page that does not exist'}
        </Typography>
      </Box>
    </React.Fragment>
  )
}

export default NotFoundPage
